.admin-panel-footer {
    border-top: 2px solid #80808025;
    padding: 24px;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 5rem;

    * {
        font-family: 'Inter', sans-serif;
        font-weight: 600;
    }
}

@media (max-width: 575px) {
    .admin-panel-footer {
        padding: 16px;
        text-align: center;
    }
}
